lumn;\n  align-items: center;\n  border-bottom: 1px solid #979797;\n}\n.mobile-menu\n{\n  display: none;\n}\n.nav .avatar-name .avatar {\n  width: 120px;\n  height: 120px;\n  padding: 10px;\n  box-sizing: border-box;\n}\n.nav .avatar-name .avatar img {\n  width: 100px;\n  height: 100px;\n}\n.nav .avatar-name .name {\n  margin-top: 10px;\n  margin-bottom: 20px;\n}\n.nav .avatar-name .name i {\n  font-size: 16px;\n  font-family: \"italic\" !important;\n  font-weight: 300;\n  color: #666666;\n}\n.nav .contents ul {\n  width: 100%;\n  padding-left: 0;\n  margin-top: 25px;\n  margin-bottom: 25px;\n}\n.nav .contents ul li,\n.nav .contents ul li a {\n  text-decoration: none;\n  font-size: 15px;\n  margin: 20px auto;\n  padding-left: 0;\n  list-style: none;\n}\n.nav .contents ul li i,\n.nav .contents ul li a i {\n  cursor: pointer;\n  color: #999999;\n  margin-right: 5px;\n}\n.nav .contents ul li span,\n.nav .contents ul li a span {\n  cursor: pointer;\n  color: #999999;\n}\n.nav .contents ul li.active span,\n.nav .contents ul li.active i {\n  color: #4a4a4a;\n}\n.nav .contents ul li:hover span,\n.nav .contents ul li:hover i {\n  color: #4a4a4a;\n}\n.site-nav-toggle {\n  display: none;\n}\n.site-nav-toggle button {\n  outline: none;\n  margin-top: 2px;\n  padding: 9px 10px;\n  background: transparent;\n  border: none;\n  user-select: none;\n}\n.site-nav-toggle button .btn-bar {\n  display: block;\n  width: 22px;\n  height: 2px;\n  background: #666666;\n  border-radius: 1px;\n}\n.site-nav-toggle button .btn-bar + .btn-bar {\n  margin-top: 4px;\n}\n.search-field {\n  overflow: hidden;\n  display: none;\n  position: fixed;\n  top: 0;\n  bottom: 0;\n  z-index: 3;\n  left: 0;\n  right: 0;\n  width: 100vw;\n  height: 100vh;\n  background-color: rgba(0, 0, 0, 0.3);\n  align-items: center;\n  flex-direction: column;\n}\n.search-field .search-container {\n  width: 40vw;\n  min-width: 500px;\n  height: 70vh;\n  margin-top: 15vh;\n  background-color: white;\n}\n.search-field .search-c